input[type="text"],
input[type="email"],
input[type="password"],
input[type="url"],
input[type="tel"],
input[type="time"],
input[type="date"],
input[type="search"],
input, textarea {
  margin: 0;
  width: 100%;
  height: 60px;
  color:var(--heading-color);
  border-radius: 0;
  background-color: transparent;
  padding: 15px 0 ;
  border: none;
  transition: all 0.4s;
}
textarea {
  height: 145px;
  resize: none;
  &::placeholder{
   color: #181B0E99!important;
   }
  @include max(767px) {
    height: 100px;
  }
}
.frado-main{
   &-field{
      p{
         margin-bottom: 10px;
         font-size: 18px;
         font-weight: 700;
         line-height: 30px;
      }
      input{
        padding: 18px 24px;
        border-radius: 10px;
        font-size: 16px;
        line-height: 24px;
        background-color: var(--light-color2);
        &::placeholder{
         color: #181B0E99;
        }
      }
      &-textarea{
         background: var(--light-color2);
         padding: 24px;
         border-radius: 10px;
         margin-bottom: 24px;
         p{
            margin-bottom: 10px;
            font-size: 18px;
            font-weight: 700;
            line-height: 30px;
         }
         &.textarea2{
            background-color: var(--light-color);
         }
      }
      &.field2{
         input{
            background-color: var(--light-color);
         }
      }
   }
}

