.frado-blog{
    &-wrap{
        padding: 30px;
        border-radius: 20px;
        margin-bottom: 24px;
        transition: all 0.4s;
        background-color: var(--light-color);
        box-shadow: 0px 4px 50px 0px #0000000D;
        &:hover{
            .frado-blog-thumb img{
                transform: scale(1.1);
            }
        }
    }
    &-thumb{
        position: relative;
        overflow: hidden;
        border-radius: 20px;
        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            transition: all 0.4s;
            border-radius: 20px;
        }
    }
    &-content{
        padding: 24px 20px 0 20px;
        h4{
            padding-bottom: 15px;
        }
    }
}