.frado-p{
    &-box{
        padding-top: 50px;
        margin-bottom: 24px;
        @include max (1199px){
            padding-top: 40px;
        }
        @include max (991px){
            padding: 30px 30px 0;
        }
        @include max (767px){
            padding: 30px 20px 0;
        }
    }
   &-wrap{
        transition: all 0.4s;
        position: relative;
    }
    &-content{
        margin-top: 50px;
        @include max (1199px){
            margin-top: 40px;
        }
        @include max (991px){
            margin-top: 30px;
        }
        h4{
            padding-bottom: 15px;
        }
    }
    &-btn{
        margin-top: 50px;
        @include max (1199px){
            margin-top: 40px;
        }
        @include max (991px){
            margin-top: 30px;
        }
    }
    &-thumb{
        @include max (991px){
            padding-top: 40px;
        }
        @include max (767px){
            padding-top: 30px;
        }
        img{
            border-radius: 20px 0 0 0;
            @include max (991px){
                border-radius: 20px 20px 0 0;
            }
            width: 100%;
        }
        &.thumb2{
            @include max (991px){
                padding-top: 0!important;
                padding-bottom: 40px!important;
            }
            @include max (767px){
                padding-top: 0!important;
                padding-bottom: 30px!important;
            }
        }
    }
    // end one
    &-wrap2{
        border-radius: 20px;
        margin-bottom: 24px;
        background-color: var(--light-color);
        &:hover{
            .frado-p-thumb2 img{
                transform: scale(1.1);
            }
            .frado-p-btn2{
                opacity: 1;
            }
        }
    }
    &-thumb2{
        position: relative;
        overflow: hidden;
        display: block;
        border-radius: 20px 20px 0 0;
        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            transition: all 0.4s;
            border-radius: 20px 20px 0 0;
        }
        &::before{
            position: absolute;
            content: "";
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            z-index: 1;
            background-color: rgba(0, 0, 0, 0.25);
            opacity: 0;
            transition: all 0.4s;
        }
        &:hover::before{
            opacity: 1;
        }
    }
    &-content2{
        padding: 24px 30px 30px 30px;
        @include max (767px){
            padding: 20px;
        }
    }
    &-btn2 {
        position: absolute;
        transform: translate(-50%, -50%);
        top: 50%;
        left: 50%;
        width: 80px;
        height: 80px;
        background-color: var(--accent-bg);
        display: flex;
        transition: all 0.4s;
        align-items: center;
        justify-content: center;
        border-radius: 100px;
        z-index: 3;
        opacity: 0;
        a{
            padding: 25px;
        }
        @include max (991px){
            width: 60px;
            height: 60px;
        }
    }
    &-tag{
        margin-top: 24px;
        display: flex;
        align-items: center;
        gap: 12px;
        flex-wrap: wrap;
    }
}